
@media only screen and (max-width: 600px) {
  .buttons {
    flex-direction: column-reverse;
    #new-quote{
      margin-bottom:10px;
    }
  }
}

.quote-text {
  i {
    font-size: 1em;
    margin-right: 0.4em;
  }
  text-align: center;

  height: auto;
  clear: both;
  font-weight: 500;
  font-size: 1.75em;
}
div {
  position: relative;
  z-index: 2;
}
.App {
  text-align: center;
  #quote-box{
    padding :25px;
   background-color: white;
   border-radius:10px;
   color :black;
   position: relative;
   max-width: 40%;
   padding: 40px 50px;
   display: table;
  }
  #author {
    align-self:end;
  }
  #tweet-quote{
    float:left;
    padding-left:5%;
  }
  #new-quote{
    border:none;
    float:right;
    color:black;
    border-radius: 10px;
    padding: 10px;
    cursor:pointer;
    font-size: 16px;
  }
  .button{
    display:flex;
    justify-content: space-between;
    min-height: 50px;
    min-width: 100px;
    .tweet-quote{
      color:black;
      border-radius: 10px;
      align-self: center;
      justify-content: center;
      padding: 10px;
      cursor:pointer;
      animation :none;
      

    }
  }
 
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

}






